const errors = {
  empty: ' ',
  invalid: ' .  : ,  .',
}

export const addressValidation = (address: string): string => {
  const regexp = /^(\d{1,2}-?[-]\s)?([--]\s?)+(,?\s{1,3})(\d{1,4})([\\\/]\d{1,4})?$/i
  if (address.trim() === '') return errors.empty
  else if (!regexp.test(address)) return errors.invalid
  else return ''
}
